.Footer {
    padding-top: 100px;
} 

.Footer ul {
    display: flex;
    justify-content: center;
    padding: 25px;
    background-color: #3A10E5;
    position: fixed;
    bottom: 0;
    width: 100%;
    list-style-type: none; /*removes bullet points*/
}

.Footer a {
    margin: 0px 25px;
    font-family: Courier, Sans-serif, Serif;
    font-size: 20px;
    font-weight: bold;
    color: white;
    text-decoration: none;
}

.Footer li:hover {
    cursor: pointer;
    color: #000000;
    transform: scale(0.9);
}
